
<template>
<v-container>
  <h1>Список организаторов</h1>
      <div class="modal"
      v-if="modal == true">
        <v-card class="pa-2 modal-card">
          Редактирование пользователя: 
          <v-text-field
            v-for="(t, index) in modalInput"
            :key="t.id"
            v-model="t.index"
            :rules="nameRules"
            :counter="10"
            :label= index
            required
          ></v-text-field>
          <div class="btn_block">
          <v-btn @click="modal = false">
            Button
          </v-btn>
          <v-btn @click="modal = false">
            Button
          </v-btn>
          <v-btn class="ban-btn" @click="modal = false">
            Забанить
          </v-btn>
          </div>

        </v-card>
      </div>
      <div class="modal"
      v-if="OrgModal == true">
        <v-card class="pa-2 modal-card">
          Добавить организатора: 
          <v-text-field
            v-for="t in TableHead"
            :key="t"
            :rules="nameRules"
            :counter="10"
            :label= t
            required
          ></v-text-field>
          <div class="btn_block">
          <v-btn @click="OrgModal = false">
            Button
          </v-btn>
          <v-btn @click="OrgModal = false">
            Button
          </v-btn>
          </div>

        </v-card>
      </div>
      <v-row>
        <v-col
          cols="4"
          md="4"
        >
          <v-text-field
            v-model="filter"
            label="Поиск по Названию/Адресу/Номеру телефона/Email'у"
            prepend-icon="mdi-magnify"
            required
          >
          </v-text-field>

        </v-col>
        <v-checkbox
          v-model="checkboxSubscriptionStatus"
          indeterminate
          :label="`Только актиная подписка`"
          @click="console.log(this.checkboxSubscriptionStatus)"
        ></v-checkbox>
      </v-row>
          <v-btn @click="OrgModal = true">
            Добавить организатора
          </v-btn>
  <v-table density="compact">
    <thead>
      <tr>
        <th
        v-for="t in TableHead"
        :key="t"
        class="text-left">
          {{t}}
        </th> 
      </tr>
    </thead>
    <tbody>
      <tr
        v-for="u in filteredList().slice((this.page - 1) * this.Pagination, this.page * this.Pagination)"
        :key="u"
        @click="modalStart(u)"
        class="table"
      >
        <td>{{ u.titel }}</td>
        <td>{{ u.alres }}</td>
        <td>{{ u.phoneNumb }}</td>
        <td>{{ u.subscription }}</td>
        <td>{{ u.subscriptionStatus }}</td>
        <td>{{ u.email }}</td>

      </tr>
    </tbody>
  </v-table>
  <v-row>
  <v-col
  >
    <v-pagination 
    :total-visible="Pagination"
    v-model="page"
    :length="Math.ceil(filteredList().length / Pagination)"></v-pagination>
  </v-col>
