<script>
export default {
  data(){
    return{
      modal: false,
      OrgModal: false,
      modalInput: [],
      checkboxSubscriptionStatus: undefined,
      page: 1,
      Pagination: 8,
      filter: '',
      TableHead: [
        "Название",
        "Адрес юридический",
        "Номер телефона",
        "Срок действия подписки",
        "Статус подписки",
        "Email",
        ],
      org: [  ]
    }
  },
  methods: {
    BtnPlus() {
      this.Pagination = this.Pagination + 2
    },
    modalStart(u){
      this.modal = true;
      this.modalInput = u;
      console.log(this.modalInput)
    },
    filteredList() {
      if (this.checkboxSubscriptionStatus != undefined) {
        return this.org.filter(org => (org.titel.toLowerCase().includes(this.filter.toLowerCase()) ||
        org.alres.toLowerCase().includes(this.filter.toLowerCase()) ||
        org.phoneNumb.toLowerCase().includes(this.filter.toLowerCase()) ||
        org.email.toLowerCase().includes(this.filter.toLowerCase())) &&
        org.subscriptionStatus.includes(this.checkboxSubscriptionStatus))
      } else
        return this.org.filter(org => org.titel.toLowerCase().includes(this.filter.toLowerCase()) ||
        org.surname.toLowerCase().includes(this.filter.toLowerCase()) ||
        org.nickname.toLowerCase().includes(this.filter.toLowerCase()) ||
        org.email.toLowerCase().includes(this.filter.toLowerCase()))
      
    }
  },
  async mounted() {
    const usersI = await fetch('https://jsonplaceholder.typicode.com/users')
    const datausers = await usersI.json();
    console.log(datausers)
    for (let i = 0; i < datausers.length; i++) {
      const newRequest = {
        titel: "Название",
        alres: "Адрес юридический",
        phoneNumb: "Номер телефона",
        subscription :"Срок действия подписки",
        subscriptionStatus: "true",
        email: "Email@",
        }
        this.org.push(newRequest);
        this.org.push(newRequest);
      }
    }
}
</script>
